<template>
    <div id="app">
        <div class="header">
            <div class="icon-circle-left left" @click="go"></div>
            {{this.$route.query.nm}}
        </div>
        <div @click="a" class="massage" v-for="(a,index) in main" :key="index">
            <div class="img"><img class="auto-img" :src="a.img.replace(/w.h/,'128.180')" alt=""></div>
            <div class="info">
                <div class="name">{{a.nm}}</div>
                <div class="enm">{{a.enm}}</div>
                <div class="pinfen">{{a.sc}}</div>
                <div class="cat">{{a.cat}}<span><img class="auto-img" src="../images/3dmax.png" alt=""></span></div>
                <div class="city">{{a.fra}}/{{a.dur}}分钟</div>
                <div class="pubDesc">{{a.pubDesc}}</div>
                <div class="detail"><img class="auto-img" src="../images/right.png" alt=""></div>
            </div>
            
            <div class="bg"><img class="auto-img" :src="a.img.replace(/w.h/,'128.180')" alt=""></div>
        </div>

        <div class="return-item">
            <div class="return">
                <router-link to=""></router-link>
            </div>

           <router-view></router-view>
        </div>
        
        <div class="wrapper-date" ref="date">
            <div class="list">
                <div class="date" v-for="(d,index) in daylist" :key="index">
                    <div @click="num(index,$event)" :class="{wrapperactive:currentindex === index}">{{d}}</div>
                </div>
            </div>
        </div>

        <div class="cinemas" v-for="(l,index) in list" :key="index">
          <div class="message">
           <div class="nm"><div class="name">{{l.nm}}</div> <div class="sell">{{l.sellPrice}}<span>元起</span></div></div>
           <div class="addr">{{l.addr}}</div>
           <div></div>
           <div class="discount">
               <div class="img"><img class="auto-img" src="data:image/png;base64," alt=""></div>
               {{l.promotion.cardPromotionTag}}
               </div>
           <div></div>
          </div>
          <div class="distance">{{l.distance}}</div>
        </div>
        

    </div>
</template>

<script>
let url = '/api/ajax/detailmovie?movieId='
let cenime = '/api/ajax/movie?forceUpdate=1559649670209&movieId='
let list ='/api/ajax/filterCinemas?movieId=1207959&movieId='
import BScroll from 'better-scroll'
export default {
    name:'info',
    data() {
        return {
            main:{},
            showdate:{},
            list:{},
            status:false,
            arr:[],
            currentindex:0,
            date:''
        }
    },
    created(){
           this.axios.get(url + this.$route.query.id).then(resposne =>{
           this.main = resposne.data
           console.log(this.main)
           this.$nextTick(() =>{
               if(!this.menuScroll) {
                   this.menuScroll = new BScroll(this.$refs.date,{
                       scrollX:true,
                       click:true,
                       bounce:true
                   })
               }else{
                   this.menuScroll.refresh()
               }
           })
           },err =>{
               alert(err)
           }),
           this.axios.get(list + this.$route.query.id + '&' + this.date).then(response =>{
               this.showdate = response.data
               console.log('showdate ==>',this.showdate);
           },err =>{
               alert(err)
           })

           this.axios.post(cenime+ this.$route.query.id + '&' + this.date).then(response =>{
               this.list = response.data.cinemas
               console.log(this.list);
           })
    },
    computed: {
        daylist(){
            var today = new Date();
            var a = new Date().toLocaleDateString();
            this.arr.push(a);
            for(var i = 0;i<7;i++){
              var newdate =today.setDate(today.getDate() +1)
              newdate = new Date(newdate).toLocaleDateString()
              this.arr.push(newdate)
            }
            
            return this.arr
        }
    },
    methods: {
        go(){
            this.$router.go(-1)
        },
        num(index,e){
            this.currentindex = index
            this.date = e.target.innerHTML
            console.log(cenime  + this.date)
        }
    },
}
</script>

<style lang="scss" rel="stylesheet" scoped>
    .header{
        background: #E54847;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: #fff;
        position: relative;
        .left{
            position: absolute;
            top: 0;
            bottom: 0;
            left: 10px;
            margin: auto auto;
            height: 20px;
            width: 20px;
        }
    }
    .massage{
        padding: 10px;
        display: flex;
        position: relative;
        overflow: hidden;
        .img{
           flex: 0 0 110px;
           height: 150px;
        }
        .info{
            div{
                color: #ddd;
                margin: 8px;
                font-size: 12px;
            }
            .name{
                margin-top: 0;
                color: #fff;
                font-size: 20px;
                font-weight: 700;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
                max-width: 150px;
            }
            .enm{
                font-size: 12px;
            }
            .pinfen{
                color: #fc0;
                font-weight: 700;
                font-size: 20px;
            }
            .cat{
                display: flex;
                span{
                    display: block;
                    flex: 0 0 42px;
                    margin:2px 0 0 5px;
                }
            }
            .city{
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
                width: 150px;
                font-size: 14px;
            }
            .detail{
                position: absolute;
                top: 0;
                right: 20px;
                bottom: 0;
                margin: auto 0;
                width: 10px;
                height: 15px;
            }
        }
        .bg{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            z-index: -2;
            filter:blur(5px);
        }
    }

    .wrapper-date{
       width: 100%;
       overflow: hidden;
       .list{
           display: flex;
           width: 915px;
           .date{
               flex: 0 0 115px;
               height: 45px;
               line-height: 45px;
               text-align: center;
               font-size: 14px;
               color: #666;
           }
       }
    }
    .wrapperactive{
        color: #E54847;
        border-bottom: 1px solid #E54847;
    }

    .cinemas{
        margin: 10px 10px;
        padding: 5px 0;
        display: flex;
        border-bottom: 1px solid #ddd;
        .message{
           flex: 1;
           overflow: hidden;
           .nm{
               display: flex;
               .name{
                   line-height: 23px;
                   font-size: 16px;
                   color: #000;
                   max-width: 160px;
                   text-overflow: ellipsis;
                   overflow: hidden;
                   white-space: nowrap;
               }
               .sell{
                   color: #E54847;
                   font-size: 18px;
                   font-weight: normal;
                   margin-left: 10px;
                   span{
                       font-size: 12px;
                   }
               }
           }
           .addr{
               color: #ddd;
               font-size: 14px;
               text-overflow: ellipsis;
               overflow: hidden;
               white-space: nowrap;
               margin: 5px 0;
           }
           .discount{
               display: flex;
               margin: 10px 0;
               font-size: 14px;
               .img{
                 flex: 0 0 15px;
                 height: 14px;
                 margin-right: 5px;
               }
           }
           
        }
        .distance{
            flex: 0 0 42px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            color: #666;
            font-size: 14px;
            margin: auto 0;
        }
    }
</style>