<template>
    <div id="app">
      <div class="body">
       <div class="better">
         <span>近期最受期待</span>
         <div class="menu" ref="menu">
             <ul class="list">
               <li v-for="(m,index) in main" :key="index"> 
                   <div class="img"><img class="auto-img" :src="m.img.replace(/w.h/,'128.180')" alt=""></div>
                   <div class="name">{{m.nm}}</div>
                   <div class="date">{{m.comingTitle}}</div>
               </li>
             </ul>
         </div>
       </div>
       <div class="box">
         <div class="movieitem" v-for="(a,index) in addr" :key="index">
          <div class="title">{{a.comingTitle}}</div>
          <div class="movieinfo">
              <div class="movieimg"><img class="auto-img" :src="a.img.replace(/w.h/,'128.180')" alt=""></div>
              <div class="massage">
                  <div class="name">{{a.nm}}</div>
                  <div class="wish"><span>{{a.wish}}</span>人想看</div>
                  <div class="star">{{a.star}}</div>
                  <div class="data">{{a.rt}}</div>
                  <div class="buycar">购票</div>
              </div>
              
          </div>
         </div>
       </div>
      </div>
    </div>
</template>


<script>
let url = '/api/ajax/mostExpected?ci=20&limit=30&offset=0&token='
let addr = '/api/ajax/comingList?ci=20&token=&limit=20&offset=0'
import BScroll  from 'better-scroll'
export default {
    name:"will",
    data() {
        return {
            main:{},
            addr:{}
        }
    },
    created(){
        this.axios.get(url).then(response =>{
            this.main = response.data.coming
            console.log(this.main);
            this.$nextTick(() =>{
                if(!this.menuScroll){
                   this.menuScroll = new BScroll(this.$refs.menu,{
                       click:true,
                       scrollX:true,
                       bounce:true
                   })
                }else{
                    this.menuScroll.refresh()
                }
            })
        }),
        this.axios.get(addr).then(response =>{
            this.addr = response.data.coming
            console.log(this.addr);
        },err =>{
            alert(err)
        })
    }
}


</script>

<style lang="scss" rel="stylesheet" scoped>
 .body{
     background: #f6f6f6;
    .better{
        overflow: hidden;
        background: #fff;
        padding: 10px 5px;
        span{
            font-size: 16px;
            color: #535353;
        }
        .list{
            margin: 10px 0;
            width:2910px;
            overflow: hidden;
            li{
                float: left;
                margin: 0 5px;
                color: #000;
                font-weight: 700;
                .img{
                 width:85px;
                 height: 115px;
                 border: 1px solid #000;
                }
                .name{
                    font-weight: 700;
                    font-size: 14px;
                    margin: 10px 0;
                    width: 85px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
                .date{
                    font-size: 12px;
                    color: #535353;
                }
            }
        }
    }
    .box{
        background: #fff;
        margin:10px 0 50px 0;
        .movieitem{
          display: flex;
          flex-direction: column;
          padding: 10px 10px 0 10px;
          font-size: 14px;
          color: #535353;
          .title{
              flex: 1;
          }
          .movieinfo{
              flex: 1;
              display: flex;
              margin: 5px 0;
              .movieimg{
                  flex: 0 0 64px;
                  height: 90px;
              }
              .massage{
                  flex: 1;
                  border-bottom: 1px solid#ddd;
                  margin-left: 5px;
                  position: relative;
                  .name{
                      color: #000;
                      font-size: 16px;
                      font-weight: 700;
                  }
                  .wish{
                      margin: 10px 0;
                      span{
                          color: #FAB412;
                          margin: 0 5px;
                      }
                  }
                  .star{
                      width:184px;
                      text-overflow: ellipsis;
                      overflow: hidden;
                      white-space: nowrap;
                  }
                  .data{
                      margin-top: 10px;
                  }
                  .buycar{
                      position: absolute;
                      top: 0;
                      bottom: 0;
                      right: 0;
                      margin: auto 0;
                      width: 47px;
                      height: 27px;
                      background: rgb(38, 139, 253);
                      color: #fff;
                      text-align: center;
                      justify-content: center;
                      align-items: center;
                      line-height: 27px;
                      border-radius: 5px;
                  }
              }
              
          }
       }
    }
    
 }
</style>