<template>
    <div id="app">
        <div class="header">影院</div>
        <div class="container">
                <div class="place"><span>广州</span> <i><img class="auto-img" src="../images/slide.png" alt=""></i></div>
                <div class="inpu" @click="look">
                   <div class="glass"><img class="auto-img" src="../images/glass.png" alt=""></div>
                   <span>搜影院</span>
                </div>
        </div>

        <div class="cenimelist" v-for="(c,index) in cinema" :key="index">
            <div class="name"> <span class="nm">{{c.nm}}</span> <span style="margin:0 5px;">{{c.sellPrice}} </span> <span style="font-size:12px; line-height:16px;">元起</span></div>
            <div class="address"><p>{{c.addr}}</p><span>{{c.distance}}</span></div>
            <div class="serve">
                <p class="rebuy">改签</p>
                <p class="snack">小吃</p>
                <p class="discount" v-show="c.tag.hasOwnProperty('vipTag')">{{c.tag.vipTag}}</p>
                <p v-for="(h,index) in c.tag.hallType" :key="index">{{h}}</p>
            </div>
            <div class="advatice" v-show="c.promotion.hasOwnProperty('cardPromotionTag')">
                <div class="img">
                  <img class="auto-img" src="data:image/png;base64," alt="">
                </div>
                {{c.promotion.cardPromotionTag}}
                </div>
        </div>
    </div>
</template>

<script>
let url = '/api/ajax/cinemaList?day=2019-05-26&offset=0&limit=20&districtId=-1&lineId=-1&hallType=-1&brandId=-1&serviceId=-1&areaId=-1&stationId=-1&item=&updateShowDay=true&reqId=1558859859491&cityId=20'
export default {
    name:"cenime",
    data() {
        return {
            cinema:{}
        }
    },
    created(){
        this.axios.get(url).then(response =>{
            this.cinema = response.data.cinemas
            console.log(this.cinema);
        })
    },
    methods: {
        look(){
            this.$router.push({path:'movie/search'})
        }
    },
}
</script>

<style lang="scss" ref="stylesheet" scoped>
    .header{
        background: #E54847;
        height: 50px;
        width: 100%;
        text-align: center;
        color: #fff;
        font-size: 20px;
        line-height: 50px;
    }
    .container{
        background: #F5F5F5;
        padding: 5px 10px;
        display: flex;
        .place{
            flex: 0 0 62px;
            display: flex;
            margin: auto 0 auto 5px;
            span{
                display: block;
                flex: 0 0 30px;
                font-size: 12px;
                
            }
            i{
                flex:  0 0 8px;
                display: block;
                height: 8px;
                margin-top: 5px;
            }
        }
        .inpu{
            flex: 1;
            margin: 0 10px;
            height: 30px;
            background: #fff;
            border-radius: 5px;
            text-align: center;
            line-height: 30px;
            display: flex;
            .glass{
                flex: 0 0 20px;
                height:20px;
                margin: auto 0;
                text-align: right;
            }
            span{
                flex: 1;
                color: #ddd;
                font-size: 12px;
            }
        }
    }
    .cenimelist{
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;
        .name{
            font-size: 16px;
            font-weight: 700;
            width: calc(100% - 10px);
            display: flex;
            .nm{
                display: block;
                max-width: 200px;
                overflow: hidden;
                color: #000;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            span{
                display: block;
                
                color: #E54847;
                font-weight: normal;
            }
        }
        .address{
          margin: 10px 0;
          color: #666;
          display: flex;
          p{
              width: calc(100% - 74px);
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
          }
          span{
              margin-left: 10px;
          }
        }
        .serve{
            display: flex;
            margin: 0 0 5px 0;
            width: calc(100% - 10px);

            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            .snack{
                color: #f90;
                border: 1px solid #f90;
            }
            p{
                margin-right: 5px;
                font-size: 13px;
                padding: 2px;
                color: rgb(65, 122, 246);
                border: 1px solid rgb(65, 122, 246);
                white-space: nowrap;
            }
        }
        .advatice{
            font-size: 12px;
            color: #666;
            display: flex;
            width: calc(100% - 10px);
            .img{
                flex: 0 0 15px;
                height: 14px;
            }
        }
    }
</style>