<template>
    <div id="app">
        <div class="show">
        <div class="movie-item" v-for="(m,index) in main" :key="index" @click="mv(m.id,m.nm)">
        <div class="img"><img class="auto-img" :src="m.img.replace(/w.h/,'128.180')" alt=""></div>
        <div class="massage">
            <div class="name">
                <span>{{m.nm}}</span>  
                <div class="three"  v-show="m.globalReleased"><img class="auto-img" src="../images/IMAX.png" alt=""></div>
                <div class="D"  v-show="m.haspromotionTag"><img class="auto-img" src="../images/3d.png" alt=""></div>
                <div class="dianying" v-show="m.preShow"><img class="auto-img" src="../images/dianying.png" alt=""></div>
            </div>
            <div class="sc"><span>{{m.sc}}</span>人想看</div>
            <div class="star">主演:{{m.star}}</div>
            <div class="showinfo">{{m.showInfo}}</div>
             <div class="buyface">购买</div>
        </div>
       
       </div>
       </div>
    </div>
</template>

<script>
let url = '/api/ajax/movieOnInfoList'

export default {
    name:"hot",
    data() {
        return {
            main:{},
            sh:false
        }
    },
    created(){
            this.axios.get(url).then(response =>{
                this.main = response.data.movieList
                console.log(this.main);
            },err =>{
                alert(err)
            })
     },
     methods: {
         mv(id,nm){
             this.$router.push({path:'/info',query:{id:id,nm:nm}})
         }
     },
    
        
}
</script>


<style lang="scss" rel="stylesheet" scoped>
  .show{
      margin-bottom: 70px;
      .movie-item{
        padding: 10px;
        position: relative;
        overflow: hidden;
        
        .img{
            width: 64px;
            height: 90px;
            position: absolute;
            top: 10px;
            left: 10px;
        }
        .massage{
            float: left;
            margin-left: 74px;
            width: calc(100% - 74px);
            height: 90px;
            position: relative;
            border-bottom: 1px solid #ddd;
            padding: 0 0 5px 0;
            .name{
                flex: calc(100% - 47px);
                font-size: 17px;
                font-weight: 700;
                overflow: hidden;
                margin: 5px 0;
                color: #333;
                span{
                    display: block;
                    float: left;
                    max-width: 115px;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: hidden;
                }
                 .three{
                     float: left;
                     width: 43px;
                     margin: 0 5px;
                 }
                 .D{
                     float: left;
                     width: 14px;
                     margin: 0 5px;
                 }
                 .dianying{
                     float: left;
                     width: 23px;
                     margin: 0 5px;
                 }
            }
            .sc{
                 width: calc(100% - 67px);
                 overflow: hidden;
                 text-overflow: ellipsis;
                 white-space: nowrap;
                 font-size: 14px;
                 margin: 10px 0;
                 color: #333;
                 span{
                     color: #FAB412;
                 }
            }
            .star{
                 width: calc(100% - 67px);
                 overflow: hidden;
                 text-overflow: ellipsis;
                 white-space: nowrap;
                 font-size: 14px;
                 margin: 5px 0;
                 color: #666666;
            }
            .showinfo{
                 width: calc(100% - 67px);
                 overflow: hidden;
                 text-overflow: ellipsis;
                 white-space: nowrap;
                 font-size: 14px;
                 margin: 5px 0;
                 color: #666666;
            }
            .buyface{
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                margin: auto 0;
                width: 47px;
                height: 27px;
                background: #F03D37;
                text-align: center;
                line-height: 27px;
                font-size: 12px;
                color: #fff;
                border-radius: 5px;
            }
        }
    }
  }  
</style>